import { NextRequest, NextResponse } from "next/server";
import supabase from "@/utils/supabase/client";

// Server-side Supabase client

// GET all subcategories
export async function GET() {
  try {
    const { data, error } = await supabase
      .from("insurance_subcategories")
      .select(`
        *,
        category:insurance_categories(id, name)
      `)
      .eq("is_active", true)
      .order("created_at", { ascending: false });

    if (error) return NextResponse.json({ error: error.message }, { status: 500 });

    return NextResponse.json({ subcategories: data });
  } catch (err: any) {
    return NextResponse.json({ error: err.message || "Internal server error" }, { status: 500 });
  }
}

// POST create a new subcategory
export async function POST(req: NextRequest) {
  try {
    const { name, category_id, description } = await req.json();
    if (!name || !category_id) return NextResponse.json({ error: "Missing required fields" }, { status: 400 });

    const { data, error } = await supabase
      .from("insurance_subcategories")
      .insert([{ name, category_id, description, is_active: true }])
      .select();

    if (error) return NextResponse.json({ error: error.message }, { status: 500 });
    return NextResponse.json({ subcategory: data[0] });
  } catch (err: any) {
    return NextResponse.json({ error: err.message || "Internal server error" }, { status: 500 });
  }
}

// PUT update a subcategory
export async function PUT(req: NextRequest) {
  try {
    const { id, name, description, category_id } = await req.json();
    if (!id) return NextResponse.json({ error: "Missing subcategory ID" }, { status: 400 });

    const { error } = await supabase
      .from("insurance_subcategories")
      .update({ name, description, category_id })
      .eq("id", id);

    if (error) return NextResponse.json({ error: error.message }, { status: 500 });
    return NextResponse.json({ success: true });
  } catch (err: any) {
    return NextResponse.json({ error: err.message || "Internal server error" }, { status: 500 });
  }
}

// DELETE (soft delete) a subcategory
export async function DELETE(req: NextRequest, { params }: { params: { id: string } }) {
  try {
    const { id } = params;
    if (!id) return NextResponse.json({ error: "Missing subcategory ID" }, { status: 400 });

    const { error } = await supabase
      .from("insurance_subcategories")
      .update({ is_active: false })
      .eq("id", id);

    if (error) return NextResponse.json({ error: error.message }, { status: 500 });
    return NextResponse.json({ success: true });
  } catch (err: any) {
    return NextResponse.json({ error: err.message || "Internal server error" }, { status: 500 });
  }
}
